function LeftButton(props) {
    const { className, onClick } = props;
    return (

        <NavigateBeforeIcon style={{ marginRight: "10px", color: "purple" }} className={className} onClick={onClick} color="primary" fontSize="large" />

    );
}

const RightButton = (props) => {
    const { className, onClick } = props
    return (
        <NavigateNextIcon style={{ color: "purple" }} className={className} onClick={onClick} color="primary" fontSize="large" />

    );
}

